@charset "UTF-8";




/*220126 start------------------------------------------------------------------------------------------*/
/* 비회원 및 Retail */
.main_container {
    margin:80px 0 0;
    #mainVisual {
        h2 {text-indent:-9999px; opacity:0; height:0;}
        .visual_wrap {
            overflow:hidden; position:relative;
            .swiper-slide {height:660px; padding:147px 0 0; box-sizing:border-box;}
            .slogan_wrap {max-width:1200px; margin:0 auto; box-sizing:border-box;}
            .visual_btn {
                position:absolute; top:480px; left:50%; z-index:2;
                button {
                    @include textBackground(13px, 20px);
                    @include backgroundSoltC();
                }
                &.swiper-button-disabled {opacity:0.4}
                &.visual_prev_btn {
                    margin:0 0 0 -600px;
                    button {background-image:url(../../images/main/visualBtn_prev.png)}
                }
                &.visual_next_btn {
                    margin:0 0 0 -542px;
                    button {background-image:url(../../images/main/visualBtn_next.png)}
                }
            }
            .visual_slide {
                color:$white;
                strong {font-size:68px; font-weight:$bold; display:block;}
                p {font-size:24px; font-weight:$medium; margin:35px 0 75px}
                .btn_wrap {
                    font-size:0;
                    a {
                        font-size:16px; line-height:46px; padding:0 10px; min-width:205px; margin:0 8px 0 0; text-align:center; border-radius:50px; display:inline-block; color:$white; border:1px solid $white; box-sizing:border-box;
                        @include transition(all, .3s);
                        &.btn_bruchure {padding:0 31px;}
                        &:hover, &:focus {
                            background-color:rgba(255, 255, 255, 0.3);
                        }
                    }
                }
            }
        }
        .shourtcut {
            max-width:1200px; margin:0 auto; padding:50px 0; box-sizing:border-box; display:flex; justify-content:space-between;
            li {
                width:276px; margin:0 31px 0 0; position:relative; flex-wrap:nowrap;
                &:last-child {margin:0;}
                &:after {
                    width:1px; height:44px; background-color:$gray2; position:absolute; top:50%; right:-15px; content:"";
                    @include translate(0, -50%);
                }
                &:last-child:after {display:none;}
            }
            li:nth-of-type(1) a {background-image:url(../../images/main/icon_retail.png);}
            li:nth-of-type(2) a {background-image:url(../../images/main/icon_financial.png);}
            li:nth-of-type(3) a {background-image:url(../../images/main/icon_parts.png);}
            li:nth-of-type(4) a {background-image:url(../../images/main/icon_software.png);}
            a {
                display:block; padding:27px 0 27px 35px; box-sizing:border-box;
                @include backgroundSolt(95%);
                @include transition(all, .3s);
                strong, span {
                    @include transition(all, .3s);
                }
                strong {font-size:19px; font-weight:$medium; color:$gray7; display:block;}
                span {
                    font-size:15px; margin:8px 0 0; padding:0 23px 0 0; display:inline-block; color:$gray5; position:relative;
                    &:after {
                        width:16px; height:16px; background-image:url(../../images/main/icon_arrow_s.png); position:absolute; top:50%; right:0; content:"";
                        @include translate(0, -50%);
                        @include backgroundSoltC();
                    }
                }
                &:hover, &:focus {
                    @include backgroundSolt(90%);
                    strong {color:$blue5;}
                    span {color:$blue5;}
                }
            }
        }
    }
    #contents {
        background-color:$gray1;
        .title_wrap {
            padding:0 0 50px; text-align:center;
            h3 {font-size:42px; color:$gray7; font-weight:$medium;}
        }
        section {padding:70px 0;}
        .default_section {
            padding:70px 0 90px; overflow-x:hidden;
            > div:nth-of-type(3) {padding:135px 0 55px;}
            .slide_wrap {max-width:1200px; margin:0 auto; overflow:visible; box-sizing:border-box;}
            .swiper-slide {max-width:300px; margin:0 12px 0 0;}
            .item {
                a {display:block; border:1px solid $gray2; border-radius:15px; background-color:$white; box-sizing:border-box;}
                p {padding:40px 40px 20px; box-sizing:border-box;}
                em {
                    display:block; font-weight:$bold; font-size:15px; margin:0 0 8px;
                    &.user_type01 {color:$blue6}
                    &.user_type02 {color:$blue7}
                }
                strong {font-size:30px; font-weight:$medium; display:block; color:$gray7;}
                span {font-size:14px; line-height:22px; min-height:44px; margin:15px 0 0; color:$gray6; display:block;}
                img {width:100%;}
            }
        }
        .banner_section {
            padding:45px 0 40px; background-image:url(../../images/main/banner_bg.jpg); color:$white; text-align:center;
            @include backgroundSoltC();
            h3 {text-indent:-99999px; opacity:0; height:0;}
            .layout_wrap div {display:inline-block;}
            a {font-size:28px; font-weight:$medium; color:$white;}
            p {
                font-size:15px; padding:0 175px 0 0; margin:15px 0 0; opacity:0.4; position:relative;
                em {position:absolute; top:0; right:0;}
                br {display:none;}
            }
        }
        .support_section {
            padding:55px 0 85px; background-color:$white;
            ul {display:flex; justify-content:space-between;}
            li {
                width:346px; flex-wrap:nowrap;
                a {padding:40px 0 10px; display:block; text-align:center;}
                strong {
                    padding:107px 0 25px; font-size:24px; color:$gray7; font-weight:$medium; display:block;
                    @include backgroundSolt(50%, 0);
                    @include transition(all, .3s);
                }
                span {font-size:14px; line-height:22px; display:block; color:$gray6;}
                &:nth-of-type(1) strong {background-image:url(../../images/main/support_icon01.png);}
                &:nth-of-type(2) strong {background-image:url(../../images/main/support_icon02.png);}
                &:nth-of-type(3) strong {background-image:url(../../images/main/support_icon03.png);}
                a:hover, a:focus {
                    strong {color:$blue5; background-position:50% 10px;}
                }
            }
        }
        .explore_section {
            padding:63px 0 70px;
            .tab_menu {
                margin:0 0 40px; text-align:center;
                li {
                    margin:0 5px; display:inline-block;
                    button {
                        font-size:18px; line-height:54px; padding:0 25px; color:#8b8b8c; border-radius:50px; font-weight:$medium; background-color:$gray1;
                        @include transition(all, .3s);
                    }
                    & button:hover, & button:focus, &.on button { color:$white; background-color:$gray7;}
                }
            }
            .tab_contents {
                .tab_content {
                    display:none;
                    &:first-child {display:block;}
                }
                .thumb_slide_wrap {
                    margin:0 0 40px; text-align:center;
                    .swiper-notification {display:none;}
                    .thumb_slide_btn {
                        display:inline-block; vertical-align:middle;
                        button {
                            @include textBackground(36px, 36px);
                            @include backgroundSoltC();
                        }
                    }
                    .thumb_slide_prev {background-image:url(../../images/main/slideBtn_prev.png)}
                    .thumb_slide_next {background-image:url(../../images/main/slideBtn_next.png)}
                    .swiper-button-disabled {
                        opacity:0.4;
                        button {cursor:default;}
                    }
                    .thumb_slide {
                        max-width:715px; margin:0 10px 0 20px; display:inline-block; vertical-align:middle; overflow:hidden;
                        .swiper-slide {width:120px;}
                        .swiper-slide button {
                            font-size:18px; line-height:41px; color:$gray7; font-weight:$medium; position:relative;
                            &:before, &:after {
                                width:0; height:3px; position:absolute; bottom:0; content:""; background-color:$gray7;
                                @include transition(all, .3s);
                            }
                            &:before {left:50%;}
                            &:after {right:50%;}
                            &:hover:before, &:hover:after, &:focus:before, &:focus:after, &.active:before, &.active:after {width:50%;}
                        }
                    }
                }
                .ATM_cont {
                    &:after {content:""; display:block; clear:both;}
                    .photo {
                        width:67.4%; max-width:798px; float:left;
                        button {
                            width:100%; border-radius:15px; overflow:hidden; position:relative;
                            &:after {
                                width:116px; height:116px; background-image:url(../../images/main/btn_play.png); position:absolute; top:50%; left:50%; content:"";
                                @include translate(-50%, -50%);
                                @include backgroundSoltC();
                            }
                            img {width:100%;}
                        }
                        ul {margin:6px 0 0; overflow:hidden;}
                        li {
                            width:calc(50% - 3px); border-radius:15px; float:left; overflow:hidden;
                            &:last-child {float:right;}
                            img {width:100%;}
                        }
                    }
                    .info {
                        width:32.6%; max-width:394px; min-height:727px; border-radius:15px; padding:70px 55px 50px; box-sizing:border-box; background-color:$white; float:right;
                        @include boxShadow(2px 4px 50px rgba(18, 19, 20, 0.15));
                        p {
                            color:$gray7;
                            strong {font-size:40px; font-weight:$medium;}
                            span {font-size:18px; line-height:26px; margin:20px 0 0; display:block; font-weight:$medium;}
                        }
                        > span {font-size:14px; line-height:20px; margin:40px 0 0; color:$gray6; display:block;}
                        ul {margin:50px 0 0; display:flex; flex-wrap:wrap;}
                        li {
                            width:109px; margin:0 20px 20px 0; text-align:center;
                            span {display:block;}
                            p {font-size:12px; line-height:14px; color:$gray6}
                        }
                    }
                }
                .parts {
                    margin:45px 0 0;
                    > strong {font-size:20px; margin:0 0 30px; color:$gray7; font-weight:$medium; display:block; text-align:center;}
                    .parts_slide {
                        .swiper-slide {
                            width:195px !important; margin:0 10px 0 0;
                            a {padding:15px 20px 20px; border-radius:10px; overflow:hidden; display:block; background-color:$white; border:1px solid $gray2; box-sizing:border-box;}
                            strong {font-size:18px; font-weight:$medium; color:$black; display:block; text-overflow:ellipsis; white-space:nowrap; overflow:hidden;}
                            div {
                                margin:15px 0 0;
                                dl {
                                    padding:0 0 0 70px; font-size:14px; line-height:18px; margin:5px 0 0; position:relative;
                                    &:first-of-type {margin:0;}
                                }
                                dt {color:$gray5; position:absolute; top:0; left:0;}
                                dd {color:$black; text-overflow:ellipsis; white-space:nowrap; overflow:hidden;}
                            }
                            img {width:100%; margin:10px 0 0;}
                        }
                    }
                }
                .popup {
                    .popup_content {max-width:1200px;}
                    .frame_wrap {
                        padding:56.25% 0 0; position:relative;
                        iframe {width:100%; height:100%; position:absolute; top:0; left:0;}
                    }
                    .btn_popup_close {
                        top:-80px; right:-80px; background-color:$white; border-radius:100%;
                        &:after {
                            width:20px; height:20px; background-image:url(../../images/common/close_x_btn.png); content:"";
                            @include posCenterSolt(50%, 50%);
                            @include backgroundSoltC();
                            @include translate(-50%, -50%);
                        }
                    }
                }
            }
        }
    }
    @include mob() {
        margin:em(124) 0 0;
        #mainVisual {
            .visual_wrap {
                .swiper-slide {
                    height:em(710); padding:0; position:relative;
                    &:before {width:100%; height:100%; background-color:rgba(0, 0, 0, 0.5); content:""; position:absolute; top:0; left:0;}
                }
                .slogan_wrap {
                    width:100%; padding:0; text-align:center; position:absolute; top:50%; left:0;
                    @include translate(0, -50%);
                }
                .visual_btn {display:none;}
                .visual_slide {
                    strong {font-size:em(80);}
                    p {font-size:em(32); line-height:em(42); margin:em(50) em(30)}
                    .btn_wrap {
                        padding:0 em(14); font-size:0;
                        a {
                            //font-size:em(26); line-height:em(86); padding:0 em(36); min-width:em(310); margin:0 em(14) 0 0; border-radius:em(50); border-width:em(2);
                            font-size:em(26); line-height:em(86); padding:0; width:calc(50% - 0.7rem); max-width:em(310); min-width:auto; margin:0 em(7); border-radius:em(50); border-width:em(2); letter-spacing:em(-0.5);
                            &:first-child {margin-left:0;}
                            &:last-child {margin-right:0;}
                            &.btn_bruchure {padding:0 em(36);}
                            &.btn_more {background-color:$blue5; border-color:$blue5;}
                        }
                    }
                }
            }
            .shourtcut {
                padding:em(60) em(30); flex-wrap:wrap;
                li {
                    width:50%; margin:0; border-bottom:1px solid $gray2; border-left:1px solid $gray2; box-sizing:border-box;
                    &:after {display:none;}
                    &:nth-of-type(2n+1) {border-left:none;}
                    &:nth-of-type(3), &:nth-of-type(4) {border-bottom:none;}
                }
                li:nth-of-type(1) a {background-image:url(../../images/main/icon_retail_m.png);}
                li:nth-of-type(2) a {background-image:url(../../images/main/icon_financial_m.png);}
                li:nth-of-type(3) a {background-image:url(../../images/main/icon_parts_m.png);}
                li:nth-of-type(4) a {background-image:url(../../images/main/icon_software_m.png);}
                a {
                    padding:27px 0 27px em(30); background-size:em(140);
                    @include backgroundSolt(100%);
                    strong {font-size:em(34); font-weight:$bold;}
                    span {
                        font-size:em(24); margin:em(15) 0 0; padding:0;
                        &:after {display:none;}
                    }
                    &:hover, &:focus {
                        @include backgroundSolt(100%);
                    }
                }
            }
        }
        #contents {
            .title_wrap {
                padding:0 0 em(50);
                h3 {font-size:em(58); line-height:em(64); font-weight:$bold;}
            }
            section {padding:em(90) em(30);}
            .default_section {
                padding:em(90) em(30);
                > div:nth-of-type(3) {padding:em(120) 0 em(50);}
                .swiper-slide { /*max-width:em(586);*/
                    max-width:85%; margin:0 em(24) 0 0;}
                .item {
                    a {border-radius:em(20);}
                    em {font-size:em(24)}
                    p {padding:em(55) em(60) em(15);}
                    strong {font-size:em(56); font-weight:$bold;}
                    span {font-size:em(32); line-height:em(42); min-height:em(84); margin:em(25) 0 0;}
                }
            }
            .banner_section {
                padding:em(50) em(30); background-image:url(../../images/main/banner_bg_m.jpg);
                a {
                    font-size:em(56); line-height:em(60); font-weight:$bold;
                    span {display:block; font-size:em(36); margin:em(15) 0 0;}
                }
                p {
                    font-size:em(28); line-height:em(32); padding:0; margin:em(20) 0 0;
                    em {display:block; position:static; margin:em(60) 0 0;}
                    br {display:block;}
                }
            }
            .support_section {
                padding:em(90) em(30);
                ul {display:block;}
                li {
                    width:100%;
                    a {
                        padding:em(20) 0 em(20) em(235); text-align:left; position:relative;
                        &:before {
                            width:em(120); height:em(120); position:absolute; top:50%; left:em(30); content:"";
                            @include translate(0, -50%);
                            @include backgroundSoltC();
                        }
                    }
                    strong {
                        padding:0 0 em(20); font-size:em(38); font-weight:$bold; background:none !important; letter-spacing:em(-2);
                    }
                    span {
                        font-size:em(30); line-height:em(36);
                        br {display:none;}
                    }
                    &:nth-of-type(1) a:before {background-image:url(../../images/main/support_icon01_m.png);}
                    &:nth-of-type(2) a:before {background-image:url(../../images/main/support_icon02_m.png);}
                    &:nth-of-type(3) a:before {background-image:url(../../images/main/support_icon03_m.png);}
                }
            }
            .explore_section {
                padding:em(50) 0;
                .tab_menu {
                    margin:0 0 em(60);
                    li {
                        margin:0;
                        button {
                            font-size:em(30); line-height:em(90); padding:0 em(40); border-radius:em(50);
                        }
                    }
                }
                .tab_contents {
                    .thumb_slide_wrap {
                        margin:0; overflow:hidden; position:relative; z-index:1;
                        &:after {width:100%; height:em(2); position:absolute; bottom:0; left:0; content:""; background-color:$gray2; z-index:-1}
                        .thumb_slide_btn {display:none;}
                        .thumb_slide {
                            max-width:100%; margin:0; padding:0 0 0 em(70); box-sizing:border-box;
                            .swiper-slide {width:auto; margin:0 em(40) 0 0;}
                            .swiper-slide button {
                                font-size:em(32); line-height:em(32); padding:0 0 em(30); font-weight:$bold;
                                &:before, &:after {height:em(4);}
                            }
                        }
                    }
                    .ATM_cont {
                        overflow:hidden; padding:em(30) em(30) 0;
                        .photo {
                            width:100%; max-width:100%;
                            button {
                                width:100%; border-radius:em(15);
                                &:after {width:em(140); height:em(140);}
                            }
                            ul {margin:em(8) 0 0;}
                            li {width:calc(50% - 0.2rem); border-radius:em(15);}
                        }
                        .info {
                            width:100%; max-width:100%; min-height:auto; margin:em(8) 0 0; border-radius:em(15); padding:em(45) 0; border:em(2) solid $gray2; box-shadow:none;
                            p {
                                padding:0 em(40);
                                strong {font-size:em(48); font-weight:$bold;}
                                span {font-size:em(30); line-height:em(34); margin:em(15) 0 0; font-weight:$bold;}
                            }
                            > span {font-size:em(26); line-height:em(34); margin:em(40) 0 0; padding:0 em(40);}
                            ul {
                                margin:em(30) 0 0; overflow-x:auto; overflow-y:hidden; width:100%; box-sizing:border-box; flex-wrap:nowrap; padding:0 em(30);
                            }
                            li {
                                width:25%; min-width:em(185); margin:0 em(40) 0 0;
                                &:last-child {margin:0;}
                                img {width:em(96);}
                                p {font-size:em(24); line-height:em(26); padding:em(10) 0 0;}
                            }
                        }
                    }
                    .parts {
                        margin:em(50) 0 0; padding:0 0 0 em(30);
                        > strong {font-size:em(30); margin:0 0 em(20); font-weight:$bold; text-align:left;}
                        .parts_slide {
                            .swiper-slide {
                                width:16rem !important; margin:0 em(10) 0 0;
                                a {padding:em(30) em(30) 0; border-radius:em(10); border-width:em(2);}
                                strong {font-size:em(28); font-weight:$bold;}
                                div {
                                    margin:em(15) 0 0;
                                    dl {
                                        padding:0 0 0 em(100); font-size:em(22); line-height:em(28); margin:em(10) 0 0;
                                    }
                                }
                                img {margin:em(10) 0 0;}
                            }
                        }
                    }
                    .popup {
                        .popup_content {padding:0 em(30)}
                        .btn_popup_close {
                            width:em(60);height:em(60); top:em(-30); right:em(15);
                            &:after {
                                width:em(30); height:em(30);
                            }
                        }
                    }
                }
                &.fix {
                    .tab_content {padding:em(65) 0 0}
                    .thumb_slide_wrap {position:fixed; top:em(124); left:0; width:100%; z-index:5; background-color:rgba(255, 255, 255, 0.8); padding:em(30) 0 0;}
                }
            }
        }
    }
}
