                .swiper-slide {
                    /*background-color:$white; border-radius:em(20);*/
                    box-sizing:border-box; padding:em(50);
                    .title {
                        margin:0 0 em(50);
                        a {
                            font-size:em(36); line-height:em(36); display:inline-block; font-weight:$bold; position:relative; padding:0 em(38) 0 0; letter-spacing:em(-2);
                            &:after {
                                width:em(32); height:em(32); position:absolute; top:50%; right:0; background-image:url(../../images/common/icon_arrow_tit_m.png); content:"";
                                @include backgroundSoltC();
                                @include translate(0, -50%);
                            }
                        }
                    }
                    .status_list {
                        overflow:hidden; margin:em(70) 0 0;
                        ul {overflow:hidden;}
